@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
.App{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: 'Nunito', sans-serif;
    background-image: linear-gradient(90deg, #1d4e89, #00b2ca );
}

body{
    padding: 0;
    margin: 0;
}

.search-input{
    margin-top: 100px;
    display: flex;
}

.search input{
    border: none;
    border-top-left-radius:5px;
    border-bottom-left-radius:5px;
    font-size:1.5rem;
    padding: 15px;
    height: 30px;
    width: 300px;
    outline:none;
}

.search-icon{
    height: 60px;
    width: 50px;
    display:grid;
    place-items: center;
    background-color: white;
    border-top-right-radius:5px;
    border-bottom-right-radius:5px;
}

.data-result{
    margin-top: 5px;
    width: 380px;
    height: 200px;
    border-radius: 5px;
    background-color: white;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
}

.data-result::-webkit-scrollbar{
    display: none;
}

.data-result .data-item{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    color:black;
}

.data-item p{
    margin-left: 10px;
}

a{
    text-decoration:none;
}

a:hover {
    background-color: lightgrey;
}

.clear-btn {
    cursor: pointer;
}

